.auth-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.fill {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.gradient-display-background-login {
  clip: rect(0px, 50vw, 100vh, 0px);
  transition: clip 1s ease-in-out;
}

.gradient-display-background-register {
  clip: rect(0px, 100vw, 100vh, 50vw);
  transition: clip 1s ease-in-out;
}

.gradient-display-background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  z-index: 1;
  background-color: white;
  background-image: url('../images/background.jpg');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.gradient-display-login-rfalse {
  z-index: 2;
  top: 0;
  left: 0px;
  opacity: 0;
  background-image: linear-gradient(
    to right,
    rgba(255, 128, 44, 0.8),
    rgba(227, 161, 67, 0.8)
  );
  transition: left 1s ease-in-out, opacity 1s ease-in-out;
}

.gradient-display-login-btrue {
  z-index: 3;
  top: 0;
  left: 0px;
  opacity: 1;
  background-image: linear-gradient(
    to right,
    rgba(15, 76, 130, 0.8),
    rgba(42, 112, 174, 0.8)
  );
  transition: left 1s ease-in-out, opacity 1s ease-in-out;
}

.gradient-display-register-rtrue {
  top: 0;
  z-index: 3;
  left: 50vw;
  opacity: 1;
  background-image: linear-gradient(
    to right,
    rgba(255, 128, 44, 0.8),
    rgba(227, 161, 67, 0.8)
  );
  transition: left 1s ease-in-out, opacity 1s ease-in-out;
}
.gradient-display-register-bfalse {
  top: 0;
  z-index: 2;
  left: 50vw;
  opacity: 0;
  background-image: linear-gradient(
    to right,
    rgba(15, 76, 130, 0.8),
    rgba(42, 112, 174, 0.8)
  );
  transition: left 1s ease-in-out, opacity 1s ease-in-out;
}

.gradient-display {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 50vw;
  height: 100%;
}

.gradient-display-text {
  z-index: 4;
  font-size: 20px;
  color: white;
}

.gradient-display-button {
  z-index: 4;
  border-radius: 50px;
  border-style: none;
  border: 2px #ffffff solid;
  color: white;
  background-color: transparent;
  font-size: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 20px;
  cursor: pointer;
  transition: background-color 0.1s ease-in-out;
}

.gradient-display-button-blue:hover {
  background-color: white;
  border: 2px #0f4c82 solid;
  color: #0f4c82;
  transition: background-color 0.1s ease-in-out;
}

.gradient-display-button-red:hover {
  background-color: white;
  border: 2px #ff802c solid;
  color: #ff802c;
}

.reg-transition-enter {
  transform: translateX(-50px);
  opacity: 0;
}

.reg-transition-enter-active {
  transform: translateX(0px);
  opacity: 1;
  transition: transform 500ms ease-in-out, opacity 500ms ease-in-out;
}

.reg-transition-exit {
  transform: translateX(0px);
  opacity: 1;
}

.reg-transition-exit-active {
  transform: translateX(-50px);
  opacity: 0;
  transition: transform 500ms ease-in-out, opacity 500ms ease-in-out;
}

.login-transition-enter {
  transform: translateX(50px);
  opacity: 0;
}

.login-transition-enter-active {
  transform: translateX(0px);
  opacity: 1;
  transition: transform 500ms ease-in-out, opacity 500ms ease-in-out;
}

.login-transition-exit {
  transform: translateX(0px);
  opacity: 1;
}

.login-transition-exit-active {
  transform: translateX(50px);
  opacity: 0;
  transition: transform 500ms ease-in-out, opacity 500ms ease-in-out;
}

.auth-notification-container {
  position: absolute;
  z-index: 6;
  width: 100%;
  padding-right: 12.085%;
  padding-left: 12.085%;
  padding-top: 20px;
}

.alert-align {
  text-align: left;
}

.auth-logo {
  position: absolute;
  z-index: 5;
  font-size: 1.25rem;
  font-weight: 600;
  font-family: 'Open Sans', sans-serif;
  left: 1rem;
  top: 0.96895rem;
  text-decoration: none;
}

.auth-logo-login {
  color: white;
  transition: color 0.5s ease-in-out;
}

.auth-logo-register {
  color: #ff802c;
  transition: color 0.5s ease-in-out;
}

.auth-logo-login:hover {
  color: #e8e8e8;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.125s ease-in-out;
}

.auth-logo-register:hover {
  color: #e3a143;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.125s ease-in-out;
}
